<template>
  <div id="singleBlog">
    <h1>博客标题:{{blog.title}}</h1>
    <h1 >博客内容:<span v-html="blog.content"></span></h1>
    <p>作者：{{blog.author}}</p>
    <p>分类：</p>
    <ul>
      <!--:key="category"优化项目-->
      <li v-for="category in blog.categories" :key="category">
        {{category}}
      </li>
    </ul>
    <button @click="deleteStringBlog()">删除</button>

    <router-link  :to="'/edit/'+id">编辑</router-link>
  </div>
</template>

<script>
  export default {
    name: "singleBlog",
    data() {
      return {
        id: this.$route.params.id,
        blog: {}
      }
    },
    created() {
      //console.log(this.id)
      this.$http.get("https://wd3230720780womtah.wilddogio.com/posts/" + this.id + ".json")
        .then(function (data) {

          return data.json();
          //this.blog = response.body
        })
        .then(function (data) {
          // console.log(data);
           this.blog = data;
        })
    },
    methods:{
      deleteStringBlog(){
        this.$http.delete("https://wd3230720780womtah.wilddogio.com/posts/" + this.id + ".json")
          .then(response=>{
            this.$router.push({path: '/'}); //路径跳转
            //this.$route.push({path:'/blog/'+ this.id + ".json"});
          })
      }
    }
  }
</script>

<style scoped>
  #singleBlog {
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



