<template>
  <div class="add-blog">
    <h2>添加博客</h2>
    <form v-if="!booleanText">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required/>
      <label>博客内容</label>
      <!--<textarea v-model="blog.content"></textarea>-->
      <vue-editor  class="editor" v-model="blog.content"></vue-editor>
      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories"/>
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories"/>
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories"/>
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories"/>
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>
    <div v-if="booleanText">
      <h3>您的博客发布成功！</h3>
    </div>
    <div class="preview">
      <h3>博客总览</h3>
      <p>博客的标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p v-html="blog.content"></p>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories">{{category}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
      <p>发布时间：{{blog.time}}</p>
    </div>
  </div>
</template>

<script>

  import {VueEditor} from 'vue2-editor'

  export default {
    name: "Addblog",
    components: {
      VueEditor
    },
    data() {
      return {
        blog: {
          title: '',
          content: '',
          categories: [],
          author: '',
          time: '',
        },
        authors: ["hemiah", "Henry", "lili"],
        booleanText: false
      }
    },
    methods: {
      post: function () {
        this.$http.post('https://wd3230720780womtah.wilddogio.com/posts.json', this.blog)
          .then(function (data) {
            var d = new Date();
            this.blog.time = d.getFullYear() + '年' + (d.getMonth() + 1) + '月' + d.getDate() + '日' + " " + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds();
            console.log(this.blog.time)
            this.booleanText = 'true'
          })
      }
    }
  }
</script>

<style scoped>
  .add-blog * {
    box-sizing: border-box;

  }

  .add-blog {
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
    background: bisque;
  }

  label {
    display: block;
    margin: 20px 0 10px;
  }

  input[type="text"], textarea, select {
    display: block;;
    width: 100%;
    padding: 8px;
  }

  textarea {
    height: 200px;
    margin-bottom: 10px;
  }

  #checkboxes label {
    display: inline-block;
    margin-top: 0;
  }

  #checkboxes input {
    display: inline-block;
    margin-right: 15px;
  }

  button {
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    padding: 14px;
    border-radius: 4px;
    font-size: 18px;
    cursor: pointer;
  }

  .preview {
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }

  h3 {
    margin-top: 10px;
  }
  .editor{
    background: #fff;
    margin: 0 0 30px 0;
  }
</style>



