<template>
  <div v-theme="'narrow'" class="show-blogs"> <!--wide是一个字符串需用单引号引起来-->
    <h1>博客纵览</h1>
    <input type="text" v-model="search" placeholder="search"/>
    <div v-for="blog in filteredBlogs" class="single-blog"><!--rainbow为自定义参数-->
      <router-link v-bind:to="'/blog/'+blog.id"><!--拼接的时候需要绑定-->
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2> <!--rainbow为自定义参数-->
        <article v-html="blog.content">
          {{blog.content | snippet}}</article>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ShowBlogs",
    data() {
      return {
        blogs: [],
        search: ''
      }
    },
    created: function () {
      this.$http.get("https://wd3230720780womtah.wilddogio.com/posts.json")
        .then(function (data) {
          // this.blog = data.blogs
         // console.log(data.json());
          return data.json();
          // this.blogs = data.body.slice(0, 10); //slice(0,10)获取前10条数据
          //console.log(this.blogs)
        })
        .then(function (data) {
          var blogsArray = [];
          for(let key in data){
            //console.log(key)
            //console.log(data[key])
            data[key].id = key;
            blogsArray.push(data[key]);
          }
          this.blogs = blogsArray;
          console.log(this.blogs)
        })
    },
    computed: {
      filteredBlogs: function () {
        return this.blogs.filter((blog) => {
          return blog.title.match(this.search);
        })
      }
    },
    filters: { //局部的过滤器
      "to-uppercase": function (value) {
        return value.toUpperCase();
      },
      "snippet": function (value) {
        return value.slice(0, 100) + '...';  //内容出现100个字，加上省略号
      }
    },
    directives: {
      'rainbow': {
        bind(el, binding, vnode) {
          el.style.color = "#" + Math.random().toString(16).slice(2, 8);
        }
      }
    }

  }
</script>

<style scoped>
  .show-blogs {
    max-width: 800px;
    margin: 0 auto;
  }

  .single-blog a {
    text-decoration: none
  }

  .single-blog {
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border: 1px dotted #aaa;
  }

  #show-blogs a{
    color: #444;
    text-decoration: none;
  }

  input[type='text']{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
  }
</style>



